.container-about {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
  margin-bottom: 80px;
  background-color: #222222;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background-color: #222222;
}

.content-about {
  background-color: #222222;
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  color: #ffffff;
  height: 100%;
  width: 80%;
}

.about-image img {
  border-radius: 50%;
  width: 400px;
  height: 400px;
  margin-right: 100px;
}

.about-content {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 3rem;
}

.about-content .position {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 36px;
  color: rgba(255, 255, 255, 0.5);
}

.about-content .name {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 4.2rem;
  color: #ffffff;
}

.about-content .text-about {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 35px;
  color: #ffffff;
}

.social-media {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.social-media button {
  background: #333333;
  border-radius: 30px;
  width: 150px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 1.2rem;
  line-height: 35px;
  text-align: center;
  color: #ffffff;
  height: 60px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.5));
}

.social-media .btn-cv {
  background: #333333;
  border: 3px solid #007ced;
}

.social-media .btn-contato {
  border: 3px solid #d5d5d5;
  border-radius: 30px;
}

.social-media button:hover {
  background: #ffffff;
  color: #000000;
  transition: 1s;
}

@media screen and (max-width: 1300px) {
  .container-about {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .about-image img {
    width: 300px;
    height: 300px;
    margin: 0 auto;
    align-self: center;
  }
  .about-image {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    width: 300px;
  }
  .content-about {
    flex-direction: column;
    width: 100%;
    height: 100%;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .about-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3rem;
  }

  .about-content .name {
    align-self: center;
    text-align: center;
  }
  .text-about {
    text-align: center;
    width: 80%;
  }
  .social-media {
    margin-bottom: 20px;
  }
}
